<template>
     <el-form :model="searchForm" ref="searchForm"  label-width="80px" >
            <el-row>
              <el-col :span="5">
                <el-form-item label="仓库" prop="warehouseUuid">
                  <el-select v-model="searchForm.warehouseUuid" filterable @change="getMerchantsList" placeholder="请选择仓库">
                    <el-option
                      v-for="(org, index) in warehouseList"
                      :key="index"
                      :label="org.warehouseName"
                      :value="org.warehouseUuid"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
                  <el-col :span="5">
                    <el-form-item label="商户" prop="merchantUuid">
                      <el-select v-model="searchForm.merchantUuid" filterable clearable @change="getBrandAndModel" placeholder="请选择商户">
                          <el-option
                            v-for="(item, index) in merchantsList"
                            :key="index"
                            :label="item.userName"
                            :value="item.userUuid"
                          />
                      </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="5">
                  <el-form-item label="品牌" prop="productBrand">
                    <el-select v-model="searchForm.productBrand" clearable filterable @change="brandChange" placeholder="请选择品牌">
                      <el-option
                        v-for="(item,index) in brandList"
                        :key="index" class="radio"
                        :value="item"
                        :label="item">{{item}}</el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span="5">
                    <el-form-item label="产品名称"  prop="productName">
                      <el-input v-model="searchForm.productName" clearable placeholder="请输入产品名称"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="5">
                    <el-form-item label="型号"  prop="productModelName">
                      <el-select v-model="searchForm.productModelName" clearable filterable  placeholder="请选择型号">
                        <el-option
                          v-for="(item,index) in modelList"
                          :key="index" class="radio"
                          :value="item"
                          :label="item">{{item}}</el-option>
                      </el-select>
                    </el-form-item>
                </el-col>
                <!--<el-col :span="5">-->
                  <!--<el-form-item label="计件费用" prop="productCost">-->
                    <!--<el-select v-model="searchForm.productCost" placeholder="请选择计件费用">-->
                      <!--<el-option class="radio" value="1" label="所有"></el-option>-->
                      <!--<el-option class="radio" value="2" label="有"></el-option>-->
                      <!--<el-option class="radio" value="3" label="无"></el-option>-->
                    <!--</el-select>-->
                  <!--</el-form-item>-->
                <!--</el-col>-->
                <el-col :span="6" class="flex-right">
                    <el-button type="primary" @click.native="search">查询</el-button>
                    <el-button @click="resetForm('searchForm')">重置</el-button>
                </el-col>
            </el-row>
          </el-form>
</template>

<script>
/**
 * User Search Component
 * ----------------------
 * Author: Calamus
 * Date: 2018.08.28
 */
import { setSession, getSession } from '@/utils/session'

export default {
  name: 'TemplateSearch',
  data () {
    return {
      // 检索界面数据
      searchForm: {
        warehouseUuid:'',
        merchantUuid:'',
        productBrand:'',
        productName:'',
        productModelName:'',
        productCost: '',
      },
      modelList:[]
    }
  },
  props: {
    warehouseList: {
      type: Array,
      required: true
    },
    merchantsList: {
      type: Array
    },
    brandList: {
      type: Array
    },
    allModelList: {
      type: Array
    },
    modelBrand: {
      type: Array
    }
  },
  watch:{
    allModelList(val) {
      this.modelList = Object.assign({},val)
    }
  },
  activated () {
    this.searchForm= {
      warehouseUuid:'',
      merchantUuid:'',
      productBrand:'',
      productName:'',
      productModelName:'',
    }
    this.searchForm.warehouseUuid = getSession('warehouseUuid')
    this.search()
  },
  mounted () {
    this.searchForm.warehouseUuid = getSession('warehouseUuid')
    this.search()
  },
  methods: {
    // 检索
    search: function () {
      this.$emit('search', Object.assign({}, this.searchForm))
    },
    // 关闭查询框
    closeSearch: function (formName) {
      this.$emit('closeSearch')
    },
    // 重置
    resetForm (formName) {
      this.$refs[formName].resetFields()
      this.searchForm.warehouseUuid = getSession('warehouseUuid')
      this.$emit('rest', Object.assign({}, this.searchForm))
    },
    // 获取商户
    getMerchantsList (warehouseUuid) {
      // this.searchForm.merchantUuid = ''
      // this.searchForm.productBrand = ''
      // this.searchForm.productModelName = ''
      // this.searchForm.warehouseUuid = warehouseUuid
      setSession('warehouseUuid',warehouseUuid)
      this.resetForm('searchForm')
      this.$emit('getMerchantsList',{warehouseUuid:warehouseUuid})
      this.$emit('getBrandModel',{warehouseUuid:warehouseUuid})

      this.searchForm.warehouseUuid = getSession('warehouseUuid')},
    // 获取品牌和型号
    getBrandAndModel () {
      this.searchForm.productBrand = ''
      this.searchForm.productModelName = ''
      this.$emit('getBrandModel',this.searchForm)
    },
    // 获取型号
    brandChange (value) {
      this.searchForm.productModelName = ''
      if (value === '') {
        this.modelList = Object.assign({},this.allModelList)
      } else {
        let model = []
        model = this.modelBrand.find(item => {
          return item.productBrand === value
        })
        this.modelList = model.productModel
      }
    }
   }
 }
</script>
<style lang="scss" scoped>
.el-form-item{
   margin-bottom:10px;
}
</style>



