<template>
  <el-dialog
    width="500px"
    title="导入计件表格"
    :visible.sync="isVisible"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :lock-scroll="true"
    @close="cancel">
    <el-form ref="checkInfo" :model="checkInfo" :rules="formRules" label-width="80px">
      <el-row>
        <el-form-item label="仓库" prop="warehouseUuid">
          <el-select v-model="checkInfo.warehouseUuid" filterable placeholder="请选择仓库">
            <el-option
                v-for="(item, index) in warehouseList"
                :key="index"
                :label="item.warehouseName"
                :value="item.warehouseUuid">
            </el-option>
          </el-select>
        </el-form-item>
      </el-row>
      <el-row>
        <el-form-item class="is-required" label="附件">
          <el-upload
            ref="uploadFile"
            class="upload-demo"
            :action="actionUrl"
            :data="checkInfo"
            :auto-upload="false"
            :on-preview="handlePreview"
            :before-upload="onBeforeUpload"
            :on-change="handleChange"
            :on-remove="handleRemove"
            :before-remove="beforeRemove"
            multiple
            :limit="1"
            :headers="myHeaders"
            :on-success="uploadSuccess"
            :on-error="uploadError"
            :on-exceed="handleExceed"
            accept="application/vnd.ms-excel, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
            :file-list="fileList">
            <el-button  size="mini" type="primary">选择附件</el-button>
          </el-upload>
        </el-form-item>
      </el-row>
      <el-row>
        <el-col :span="24" class="flex-right">
          <el-button type="primary" @click="save()" :loading="isSaving">提交</el-button>
          <el-button @click="cancel()">取消</el-button>
        </el-col>
      </el-row>
    </el-form>
  </el-dialog>
</template>

<script>
  import {importPiece} from '@/api/warehouse'
  // api
  import { BASE_PER_FIX_URL } from '@/utils/baseUrlPerFix.js'
  import { setSession, getSession } from '@/utils/session'
  import { getToken } from '@/utils/auth.js'

  const token = getToken()
  export default {
    name: 'UploadExcel',
    data() {
      return {
        isVisible: false,
        checkInfo: {
          warehouseUuid:''
        },
          formRules: {
              warehouseUuid: [{required: true, message: '请选择仓库', trigger: 'blur'}],
          },
        //上传文件
        myHeaders: {Authorization: token}, // 添加认证码
        fileList:[],
        actionUrl: BASE_PER_FIX_URL+'/product/cost/import',
        isSaving:false
      }
    },
    props: {
      show: {
        type: Boolean,
        required: true,
        default: false
      },
        warehouseList: {
            type: Array
        }
    },
    watch: {
      show(isShow) {
        this.isVisible = isShow
        this.checkInfo.warehouseUuid = getSession('warehouseUuid')
      }
    },
    methods: {
      save() {
        this.$refs.checkInfo.validate(isValid => {
          if (isValid) {
             if(this.fileList.length > 0){
                this.$refs.uploadFile.submit();
              }else{
                this.$message({
                  showClose: true,
                  message: '请上传附件',
                  type: 'warning'
                })
              }
          }
        })
      },
      cancel() {
        this.isVisible = false
        this.$refs.checkInfo.resetFields()
        this.fileList = []
        this.$refs.uploadFile.clearFiles()
        this.$emit('update:show', false)
      },
      //上传文件
      handleRemove(file, fileList) {
        this.fileList = fileList
      },
      handlePreview(file) {

      },
      handleExceed(files, fileList) {
        this.$message.warning(`当前限制选择 1 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
      },
      beforeRemove(file, fileList) {
        // return this.$confirm(`确定移除 ${ file.name }？`);
      },
      handleChange(file,fileList){
        this.fileList = fileList
      },
      uploadSuccess (res,file,fileList) {
        if(res.head.code == 200 && res.head.success == 'true'){
          this.$message({
            type: 'success',
            message: '表格已导入！'
          })
          this.cancel()
          this.$emit('updateList')
        } else if(res.head.code == 201 && res.head.success == 'false'){
          this.$message({
            type: 'warning',
            message: res.head.msg
          })
          this.cancel()
          this.$emit('updateList')
        }
      },
      uploadError (err) {
        this.$message({
          type: 'error',
          message: '导入表格失败！'
        })
      },
      onBeforeUpload (file) {
        // let isExcel = (file.type === 'application/vnd.ms-excel') || (file.type ==='application/vnd.openxmlformats-officedocument.spreadsheetml.sheet')
        // if (!isExcel) {
        //   this.$message({
        //     type: 'error',
        //     message:'上传文件只能是表格格式!'
        //   });
        // }
        // return isExcel
      },
    }
  }
</script>

<style lang="scss" scoped>
  .recordList {
    border-bottom: 1px solid #e7e7e7;
  }
  .result{
    font-size: 12px;
    margin-right: 10px;
    border: 1px solid;
    line-height: 15px;
    border-radius: 5px;
    height: 15px;
    margin-top: 4px;
    padding: 0 4px;
    color: #fff;
  }
  .fail {
    border-color: #F56C6C;
    background-color: #F56C6C;
  }

  .success {
    border-color: #67C23A;
    background-color: #67C23A;
  }
</style>



