<template>
    <div>
      <!-- 查询 -->
      <common-wrapper>
        <template-search class="search"
          :warehouseList="orgnization"
          :merchantsList="merchantsList"
          :modelBrand="modelBrand"
          :brandList="brandList"
          :allModelList="modelList"
          @search="queryList"
          @rest="rest"
          @getMerchantsList="getMerchantsList"
          @getBrandModel="getBrandModel"/>
      </common-wrapper>
      <!-- <header-breadcrumb :breadData="$route.matched" /> -->
      <nav-button>
        <el-button-group>
          <el-button icon="fdddfont icon-add-new" @click="handleAdd">新增模版</el-button>
          <el-button icon="fdddfont icon-shujudaoru" @click="handleImport">产品导入</el-button>
          <el-button icon="el-icon-download" :disabled="exportDisabled" @click="handleExport">模板导出</el-button>
          <el-button icon="fdddfont icon-navicon-ywcs" @click="handleCharge">计件收费标准导入</el-button>
        </el-button-group>
      </nav-button>

     <!-- 产品列表 -->
      <common-wrapper>
        <common-table :flex="310">
          <template slot="table">
            <el-table
              ref="userTable"
              height="100%"
              stripe
              :border="true"
              tooltip-effect="light"
              v-loading="isListLoading"
              :data="tableData"
              @row-click="getTemplateDetail"
              @selection-change="selected => selectRow = selected">
                <!-- <el-table-column type="selection" width="30" ></el-table-column> -->
                <!-- <el-table-column type="index" :index="indexMethod" label="序号" width="50"></el-table-column> -->
                <el-table-column sortable prop="merchantUuid" label="商户" :formatter="formatMerchant" width="120"></el-table-column>
                <el-table-column sortable prop="productBrand" label="品牌"></el-table-column>
                <el-table-column sortable prop="productName" label="产品名称" min-width="100px"></el-table-column>
                <el-table-column prop="productModel" label="型号"></el-table-column>
                <el-table-column sortable prop="productStandard" label="规格" ></el-table-column>
                <el-table-column prop="productColor" label="颜色"></el-table-column>
                <el-table-column sortable prop="productCount" label="包装数量" min-width="100px"></el-table-column>
                <el-table-column  prop="allProductItemName" label="子件名" width="80" :show-overflow-tooltip="true">
                  <!-- <template slot-scope="scope">
                    <el-tooltip placement="top" effect="light" enterable :visible-arrow="false">
                      <div slot="content" >
                        <div style="max-width:400px">
                          {{getItemsName(scope.row)}}
                        </div>
                      </div>
                      <span class="overflow">{{getItemsName(scope.row)}}</span>
                    </el-tooltip>
                  </template> -->
                </el-table-column>
                <el-table-column prop="productType" label="货品类型" :formatter="typeFormatter"></el-table-column>
                <!--<el-table-column prop="productMain" label="计件类型" :show-tooltip-when-overflow="true" width="80">-->
                  <!--<template slot-scope="scope">-->
                    <!--<el-tooltip placement="top" effect="light" enterable :visible-arrow="false">-->
                      <!--<div slot="content" >-->
                        <!--<div style="max-width:400px">-->
                          <!--{{scope.row.productMain}}-->
                        <!--</div>-->
                      <!--</div>-->
                      <!--<span class="overflow">{{scope.row.productMain}}</span>-->
                    <!--</el-tooltip>-->
                  <!--</template>-->
                <!--</el-table-column>-->
                <!--<el-table-column prop="productChecked" label="质检类型" :formatter="checkFormatter"></el-table-column>-->
                <!--<el-table-column sortable prop="productCost" label="计件收费" min-width="100px"></el-table-column>-->
                <!-- <el-table-column prop="productRemarks" label="备注"></el-table-column> -->
                <el-table-column label="操作" width="190" >
                  <template slot-scope="scope">
                    <el-tooltip class="item" effect="light" content="编辑" placement="bottom">
                      <el-button size="mini" round icon="fdddfont icon-modify" @click.stop="handleEdit(scope.$index, scope.row)"></el-button>
                    </el-tooltip>
                    <el-tooltip class="item" effect="light" content="删除" placement="bottom">
                      <el-button size="mini" round icon="fdddfont icon-delete"   @click.stop="handleDel(scope.$index, scope.row)"></el-button>
                    </el-tooltip>
                  </template>
                </el-table-column>
            </el-table>
          </template>
          <template slot="pagination">
                <el-pagination   background   layout="total, prev, pager, next"
                :current-page.sync="pagination.pageNum"
                :page-size="pagination.pageSize"
                :total="pagination.total"
                @current-change="changePage"
                ></el-pagination>
          </template>
        </common-table>
      </common-wrapper>

     <!-- 新增、修改, 因二者格式不同,故分开两个组件调用 -->
     <template-create
       :show.sync="isCreateFormVisible"
       :merchantsList = "merchantsList"
       :searchParams="searchParams"
       :brandList="brandList"
       @updateList="queryList"
       @getBrandModel="getBrandModel"
     />
     <template-edit
        :show.sync="isEditFormVisible"
        :merchantsList = "merchantsList"
        :searchParams="searchParams"
        :brandList="brandList"
        :modifyTemplate.sync="modifyTemplate"
        @updateList="queryList"
        @getBrandList="getBrandList"
        @getBrandModel="getBrandModel"
     />

      <!-- 产品导入 -->
      <import-template
       :show.sync="importVisible"
       :warehouseList="orgnization"
       :merchantsList = "merchantsList"
       @getMerchantsList="getMerchantsList"
       @updateList="queryList"
     />

     <!-- 计件收费标准 -->
     <charge-rule
      :show.sync="chargeVisible"
      @updateList="queryList"
     />
      <upload-excel
          :warehouseList="orgnization"
          :show.sync="showUploadDialog"
      />
    </div>
</template>

<script>
/**
 * Product Template Module
 * ----------------------
 * Author: Calamus
 * Date: 2018.08.28
 */
import moment from 'moment'
// 全局混入
import {mixins} from '@/mixins'
// 自定义组件
import NavButton from '@/components/navButton'
import NavPremissonButton from '@/components/navButton/navPremissonButton'
import CommonWrapper from '@/components/commonWrapper'
import CommonTable from '@/components/commonTable'
import TemplateSearch from './Components/Search'
import TemplateCreate from './Components/TemplateCreate'
import TemplateEdit from './Components/TemplateEdit'
import ImportTemplate from './Components/ImportTemplate'
import ChargeRule from './Components/ChargeRule'
import UploadExcel from './Components/UploadExcel'
import { setSession, getSession } from '@/utils/session'

// API
import { getAllOrgList, getAllMerchantsList, getBrandModel } from '@/api/common'

import { getWarehouseList, getProducetTemplateList, delTemplate, editTemplate, downloadTemplate, uploadTemplate,templateExport } from '@/api/warehouse'

export default {
  name: 'ProductTemplate',
  components: {
    NavButton,
    NavPremissonButton,
    CommonWrapper,
    CommonTable,
    TemplateSearch,
    TemplateCreate,
    TemplateEdit,
    ImportTemplate,
    ChargeRule,
      UploadExcel
  },
  data () {
    return {
      comp: this,
      buttonData: [],
      tableData: [],
      selectRow: [], // 列表选中列
      isListLoading: false, // 列表加载
      orgnization: this.$store.getters.warehouseList, // 公用组织机构
      merchantsList: [], // 公用商户列表
      searchMerchantsList: [],
      modelBrand: [],
      brandList: [], // 公用品牌列表
      modelList: [],
      isCreateFormVisible: false, // 新增界面是否显示
      isEditFormVisible: false, // 编辑界面是否显示
      isRoleAuthVisible: false, // 用户角色授权窗口是否展示
      importVisible: false, // 产品导入界面是否展示
      chargeVisible: false, // 计件收费标准页面是否展示
      modifyTemplate: null, // 弹出框编辑时存放的临时行数据
      pagination: { // 翻页数据
        pageNum: 1,
        pageSize: 10,
        total: 0
      },
      searchParams: {}, // 查询参数
        showUploadDialog:false
    }
  },
  activated () {
    this.$refs.userTable.doLayout()
  },
  mounted () {
    let params = {warehouseUuid: getSession('warehouseUuid')}
    this.getMerchantsList(params)
    this.getBrandModel(params)
  },
  computed: {
    exportDisabled(){
      if(this.searchParams.merchantUuid){
        return false
      }else{
        return true
      }
    }
  },
  methods: {
    // 查询和初始化查询
    // 查询/翻页公用方法
    queryList (params) {
      var searchParams
      if (params) {
        searchParams = this.searchParams = params
      } else {
        searchParams = this.searchParams
      }
      // this.merchants(params).then(res => {
      //   this.merchantsList = res;
      this.getProductTemplate(
        {
          ...searchParams,
          pageNum: this.pagination.pageNum,
          pageSize: this.pagination.pageSize
        }
      )
      // })
      // this.getMerchantsList({warehouseUuid:searchParams.warehouseUuid,abc:'111'});
    },
    // 获取商户列表
    getMerchantsList (params) {
      this.merchants(params).then(res => {
        this.merchantsList = res
      })
    },
    merchants (params) {
      return getAllMerchantsList(params)
    },
    getModelList () {

    },
    getBrandList () {

    },
    // 获取品牌型号列表
    getBrandModel (params) {
      getBrandModel(params).then(res => {
        if (!res.error) {
          this.brandList = []
          this.modelList = []
          res.forEach((item, index) => {
            this.brandList.push(item.productBrand)
            item.productModel.forEach(item2 => {
              this.modelList.push(item2)
            })
          })
          this.modelBrand = res
        }
      })
    },
    // 获取模板列表
    getProductTemplate (params) {
      this.isListLoading = true
      getProducetTemplateList(params).then(res => {
        if(res.item.length == 0 && this.pagination.pageNum > 1){
          params.pageNum = 1
          this.pagination.pageNum = 1
          this.getProductTemplate (params)
          return
        }
        this.tableData = res.item
        this.getItemsName(this.tableData)
        this.pagination.total = res.total
        this.pagination.pageNum = res.pageNum
        this.pagination.pageSize = res.pageSize
        this.isListLoading = false
      })
    },
    getItemsName (data) {
      data.forEach(item => {
        if (item.productType == '1') {
          let obj = []
          obj = item.productItems.map((item) => {
            return item.productItemName
          })
          item.allProductItemName = obj.join(',')
        }
      })
    },
    rest (params) {
      const searchParams = this.searchParams = params
      this.getProductTemplate(
        {
          ...searchParams,
          pageNum: 1,
          pageSize: this.pagination.pageSize
        }
      )
    },
    // 显示编辑界面
    handleEdit (index, row) {
      this.modifyTemplate = row
      this.isEditFormVisible = true
    },
    // 显示新增界面
    handleAdd () {
      this.isCreateFormVisible = true
    },
    // 显示产品导入界面
    handleImport () {
      this.importVisible = true
    },
    // 显示计件收费标准
    handleCharge () {
        this.showUploadDialog = true
    },
    // 单行点击查看详情
    getTemplateDetail (row, event) {
      // console.log(row)
    },
    // 单行删除
    handleDel (index, row) {
      this.$confirm('确认删除该模版吗?', '提示', {
        type: 'warning'
      }).then(() => {
        this.delTemplate({productUuid: row.productUuid})
      }).catch(() => {})
    },
    // 批量删除
    batchRemove () {
      this.$confirm('确认删除选中的模版吗？', '提示', {
        type: 'warning'
      }).then(() => {
        let userIds = []
        this.selectRow.map(row => userIds.push(row.userUuid))
        this.isListLoading = true
        this.delTemplate(userIds).then(() => {
          this.isListLoading = false
        })
      }).catch(() => {})
    },
    // 删除接口封装
    delTemplate (userIds) {
      return delTemplate(userIds).then((res) => {
        this.$message({
          message: '删除成功',
          type: 'success'
        })
        this.queryList()
      })
    },
    // 列表翻页
    changePage (pageNum) {
      this.pagination.pageNum = pageNum
      this.queryList(this.searchParams)
    },
    // 生成列表序号
    indexMethod (index) {
      return (index + 1) + (this.pagination.pageNum - 1) * this.pagination.pageSize
    },
    // 货品类型
    typeFormatter (row) {
      return row.productType == '0' ? '小件' : row.productType == '1' ? '套件' : row.productType == '2' ? '单品' : '未知'
    },
    // 质检数据转换
    checkFormatter (row) {
      return row.productChecked === '1' ? '是' : row.productChecked === '0' ? '否' : '未知'
    },
    formatMerchant (row) {
      let user = this.merchantsList.find(item => item.userUuid === row.productMerchant)
      if (user) {
        return user.userName
      } else {
        return '---'
      }
    },
    handleExport(){
      let params = this.searchParams
      templateExport(params).then((res) => { // 处理返回的文件流
        const content = res.data
        const blob = new Blob([content], {type: 'application/ms-excel'})
        const fileName = '货品模板' + moment(new Date()).format('YYYYMMDD') + '.xls'
        if ('download' in document.createElement('a')) { // 非IE下载
          const elink = document.createElement('a')
          elink.download = fileName
          elink.style.display = 'none'
          elink.href = URL.createObjectURL(blob)
          document.body.appendChild(elink)
          elink.click()
          URL.revokeObjectURL(elink.href) // 释放URL 对象
          document.body.removeChild(elink)
        } else { // IE10+下载
          navigator.msSaveBlob(blob, fileName)
        }
      })
    }
  }

}
</script>
<style>
.overflow{
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

</style>



